import React, { Fragment } from "react";

import Recipe from "../components/Recipe";

import { sample } from "../utils";

const Welcome = (props) => {
  const nRecipes = 3;
  const selectedRecipes = sample(props.recipes, nRecipes);
  return (
    <Fragment>
      <section id="welcome">
        <header>
          <img src="/paella-playa-2019.jpg" alt="Una paella de papá" />
          <h2>RECETAS FAMILIARES</h2>
        </header>
        <p>
          Una página web para compartir las recetas familiares de los Monteros
          Reinas
        </p>
      </section>

      <section id="latest-posts">
        <h2>Una Selección de Nuestras Recetas</h2>
        <ul>
          {selectedRecipes.map((r) => {
            return (
              <li key={r.key}>
                <Recipe images={r.images} title={r.name} id={r.key} />
              </li>
            );
          })}
        </ul>
      </section>
    </Fragment>
  );
};

export default Welcome;
