import { Link } from "react-router-dom";

import "./Recipe.css";
import { imgPath } from "../utils";

const Recipe = (props) => {
  return (
    <div className="post">
      <Link to={`/recipes/${props.id}`}>
        <h3>{props.title}</h3>
        <img src={imgPath(props.images[0])} alt={props.title} />
      </Link>
    </div>
  );
};

export default Recipe;
