import React, { Fragment } from "react";

import { useParams } from "react-router-dom";

import "./RecipeDetail.css";

import { imgPath } from "../utils";

const RecipeDetail = (props) => {
  const params = useParams();

  if (props.recipes.length === 0) {
    return <Fragment></Fragment>;
  }
  const recipe = props.recipes.filter((r) => r.key === params.recipeId)[0];
  
  return (
    <Fragment>
      <section id="summary">
        <h2>{recipe.name}</h2>
        {/* <div>
        <h3>Tags</h3>
        List of tags
      </div> */}
        <article>
          <img src={imgPath(recipe.images[0])} alt="Imagen de la Receta" />
          { recipe.origin &&
          <div className="origin">
            {recipe.origin}
          </div> 
          }
          {/* <div>
          Last Updated on <time>10th April</time>
        </div> */}
        </article>
      </section>

      <div className="main">
        {recipe.images.map((img, i) => (
          <img src={imgPath(img)} alt="Imagen de la Receta" key={i} />
        ))}
      </div>
    </Fragment>
  );
};

export default RecipeDetail;
