const imgPath = (name) => {
    return `/data/images/${name}`;
};

const sample = (arr, n) => {
    const sample_obs = [];
    const newArr = [...arr];
    for (let i = 0; i < n; i++) {
        if (newArr.length === 0) {
            return sample_obs;
        }
        const sampleIdx = Math.floor(Math.random() * newArr.length);
        sample_obs.push(newArr[sampleIdx]);
        // remove to avoid repeating observations
        newArr.splice(sampleIdx, 1);
    }
    return sample_obs;
}


export { imgPath, sample };