import Recipe from "../components/Recipe";

import "./Recipes.css";

const Recipes = (props) => {
  return (
    <section id="all-posts">
      <h2>Listado de Recetas</h2>
      <ul>
        {props.recipes.map((r) => {
          return (
            <li key={r.key}>
              <Recipe images={r.images} title={r.name} id={r.key} />
            </li>
          );
        })}
      </ul>
    </section>
  );
};

export default Recipes;
